'use client';

import Link from 'next/link';
import { useEffect, useState } from 'react';
import type { Job } from '@/lib/api';
import { searchJobs, getJob, logCallSimple, markCalled } from '@/lib/api';

type Row = Partial<Job> & { tag: string };

// ---------- small helpers (no template strings) ----------
function normProc(s?: string) {
  const v = String(s || '').toLowerCase();
  if (v.includes('cape') && !v.includes('skull')) return 'Caped';
  if (v.includes('skull')) return 'Skull-Cap';
  if (v.includes('euro')) return 'European';
  if (v.includes('standard')) return 'Standard Processing';
  return '';
}
function suggestedProcessingPrice(proc?: string, beef?: boolean, webbs?: boolean) {
  const p = normProc(proc);
  const base =
    p === 'Caped' ? 150 :
    ['Standard Processing', 'Skull-Cap', 'European'].includes(p) ? 130 : 0;
  if (!base) return 0;
  return base + (beef ? 5 : 0) + (webbs ? 20 : 0);
}
function toInt(val: unknown) {
  const n = parseInt(String(val ?? '').replace(/[^0-9]/g, ''), 10);
  return Number.isFinite(n) && n > 0 ? n : 0;
}
function specialtyPriceFromRow(j: Partial<Job>) {
  if (!j.specialtyProducts) return 0;
  const ss = toInt(j.summerSausageLbs);
  const ssc = toInt(j.summerSausageCheeseLbs);
  const jerky = toInt(j.slicedJerkyLbs);
  return ss * 4.25 + ssc * 4.60 + jerky * 15.0;
}
function fmtMoney(n: number | string | undefined | null) {
  const num = typeof n === 'number' ? n : Number(String(n ?? '').replace(/[^0-9.\-]/g, ''));
  if (!isFinite(num)) return 'â€”';
  return num.toLocaleString('en-US', { style: 'currency', currency: 'USD' });
}
type Reason = 'Meat Ready' | 'Cape Ready' | 'Webbs Ready' | '-';
function reasonForRow(j: Partial<Job>): Reason {
  const st = String(j.status || '').toLowerCase();
  const cp = String(j.capingStatus || '').toLowerCase();
  const wb = String(j.webbsStatus || '').toLowerCase();
  if (st.includes('finished')) return 'Meat Ready';
  if (cp.includes('caped')) return 'Cape Ready';
  if (wb.includes('delivered')) return 'Webbs Ready';
  return '-';
}

// ---------- page ----------
export default function CallReportPage() {
  const [rows, setRows] = useState<Row[]>([]);
  const [notes, setNotes] = useState<Record<string, string>>({});
  const [loading, setLoading] = useState(false);
  const [err, setErr] = useState<string | null>(null);

  async function load() {
    setLoading(true);
    setErr(null);
    try {
      const res = await searchJobs('@report'); // Finished / Caped / Delivered
      setRows((res.rows || []).map((r: any) => ({ ...r })));
    } catch (e: any) {
      setErr(e?.message || 'Failed to load report');
      setRows([]);
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    load();
  }, []);

  function paidText(j: Row) {
    return j.Paid || j.paid || (j.paidProcessing && j.paidSpecialty) ? 'Yes' : 'No';
  }

  function displayPrice(j: Row) {
    const got: any = (j as any).price ?? (j as any).Price ?? undefined;
    if (got != null && String(got).trim() !== '') return fmtMoney(got);
    const proc = suggestedProcessingPrice(j.processType, !!j.beefFat, !!j.webbsOrder);
    const spec = specialtyPriceFromRow(j);
    return fmtMoney(proc + spec);
  }

  function setNote(tag: string, v: string) {
    setNotes((p) => ({ ...p, [tag]: v }));
  }

  async function refreshOne(tag: string) {
    try {
      const r = await getJob(tag);
      if (r?.exists && r.job) {
        setRows((prev) => prev.map((x) => (x.tag === tag ? { ...x, ...r.job } : x)));
      } else {
        setRows((prev) => prev.filter((x) => x.tag !== tag));
      }
    } catch {
      // ignore
    }
  }

  // Flips appropriate status to "Called" and (optionally) logs note
  async function onMarkCalled(row: Row) {
    const tag = row.tag!;
    const note = (notes[tag] || '').trim();
    try {
      await markCalled({ tag, scope: 'auto', notes: note });
      if (note) {
        await logCallSimple({ tag, reason: reasonForRow(row), notes: note });
      }
      setNote(tag, '');
      await refreshOne(tag);
    } catch (e: any) {
      alert(e?.message || 'Failed to mark as called.');
    }
  }

  // Increments attempts + appends notes (no status change)
  async function onPlusAttempt(row: Row) {
    const tag = row.tag!;
    const note = (notes[tag] || '').trim();

    // optimistic
    setRows((prev) =>
      prev.map((r) =>
        r.tag === tag
          ? {
              ...r,
              callAttempts: Number(r.callAttempts || 0) + 1,
              callNotes: note ? ((r.callNotes ? r.callNotes + '\n' : '') + note) : r.callNotes,
            }
          : r
      )
    );

    try {
      await logCallSimple({ tag, reason: reasonForRow(row), notes: note });
      setNote(tag, '');
      await refreshOne(tag);
    } catch (e: any) {
      alert(e?.message || 'Failed to add attempt.');
      await refreshOne(tag);
    }
  }

  return (
    <main style={{ display: 'grid', gap: 14 }}>
      <div style={{ display: 'flex', alignItems: 'center', gap: 12, flexWrap: 'wrap' }}>
        <h1 style={{ margin: 0 }}>Call Report</h1>
        <button className="btn" onClick={load} disabled={loading}>
          {loading ? 'Refreshingâ€¦' : 'Refresh'}
        </button>
        <span className="muted">Finished / Caped / Delivered</span>
      </div>

      {err ? (
        <div className="card" style={{ borderColor: '#ef4444' }}>
          Error: {err}
        </div>
      ) : null}

      <div className="card" style={{ padding: 0, overflow: 'hidden' }}>
        <table className="table" style={{ width: '100%', margin: 0, borderCollapse: 'separate', borderSpacing: 0 }}>
          <thead>
            <tr>
              <th style={{ width: 110, padding: '10px 12px' }}>Tag</th>
              <th style={{ padding: '10px 12px' }}>Name</th>
              <th style={{ width: 160, padding: '10px 12px' }}>Phone</th>
              <th style={{ width: 150, padding: '10px 12px' }}>Reason</th>
              <th style={{ width: 120, padding: '10px 12px' }}>Price</th>
              <th style={{ width: 80, padding: '10px 12px' }}>Paid</th>
              <th style={{ width: 120, padding: '10px 12px' }}># Contacts</th>
              <th style={{ minWidth: 520, padding: '10px 12px' }}>Contact Notes</th>
              <th style={{ width: 260, padding: '10px 12px' }}>Actions</th>
            </tr>
          </thead>
          <tbody>
            {rows.length === 0 ? (
              <tr>
                <td colSpan={9} style={{ padding: 16 }}>Nothing to call right now.</td>
              </tr>
            ) : null}

            {rows.map((r) => {
              const reason = reasonForRow(r);
              return (
                <tr key={r.tag} style={{ background: 'transparent' }}>
                  <td style={{ padding: '10px 12px' }}>
                    <Link href={'/intake?tag=' + encodeURIComponent(r.tag!)} title="Open form">
                      {r.tag}
                    </Link>
                  </td>
                  <td style={{ padding: '10px 12px' }}>{r.customer || 'â€”'}</td>
                  <td style={{ padding: '10px 12px' }}>{r.phone || 'â€”'}</td>
                  <td style={{ padding: '10px 12px' }}>{reason}</td>
                  <td style={{ padding: '10px 12px' }}>{displayPrice(r)}</td>
                  <td style={{ padding: '10px 12px' }}>{paidText(r)}</td>
                  <td style={{ padding: '10px 12px' }}>{Number(r.callAttempts || 0)}</td>
                  <td style={{ padding: '10px 12px' }}>
                    <div style={{ display: 'grid', gap: 8 }}>
                      <input
                        value={notes[r.tag!] || ''}
                        onChange={(e) => setNote(r.tag!, e.target.value)}
                        placeholder="e.g., Pickup Fri 4â€“6pm"
                        style={{
                          width: '100%',
                          padding: '10px 12px',
                          borderRadius: 10,
                          border: '1px solid var(--border, #cbd5e1)',
                          background: '#fff',
                          color: '#0b0f12',
                        }}
                      />
                      {r.callNotes ? (
                        <div
                          style={{
                            background: 'var(--bg-elev, #f8fafc)',
                            border: '1px solid var(--border, #e6e9ec)',
                            borderRadius: 10,
                            padding: 12,
                            maxHeight: 180,
                            overflow: 'auto',
                            whiteSpace: 'pre-wrap',
                            color: 'var(--text, #0b0f12)',
                          }}
                        >
                          {r.callNotes}
                        </div>
                      ) : null}
                    </div>
                  </td>
                  <td style={{ padding: '10px 12px' }}>
                    <div style={{ display: 'flex', gap: 8, flexWrap: 'wrap' }}>
                      <button type="button" className="btn" onClick={() => onMarkCalled(r)}>
                        Mark Called
                      </button>
                      <button type="button" className="btn secondary" onClick={() => onPlusAttempt(r)}>
                        +1 Attempt
                      </button>
                    </div>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </main>
  );
}

